
  @media only screen and (min-device-width: 1200px) {
    ::-webkit-scrollbar {
      width: 10px;
    }
  
  ::-webkit-scrollbar-track {
  
      /* box-shadow: inset 0 0 5px red;  */
      border-radius: 10px;
      background-color:transparent;
    }
  
    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: #3B3A4A;
      border-radius: 5px;
    }
  
    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: cyan;
    }

  }